import back from '@/components/back'

export default {
    components:{
        back
    },
    data(){
        return{
            date: '',
            time1: '',
            time2: '',
            number: '',
            loop: false,
            Id: '',
            readonly: false,
            user: this.$store.state.userInfo.Data,
            timing: [
                {
                    val: 'm',
                    name: '上午'
                },
                {
                    val: 'a',
                    name: '下午'
                },
                {
                    val: 'e',
                    name: '晚上'
                }
            ],
            time: '',
            list:[
                [],
                [
                    '00','30'
                ]
            ],
            arr:[],
            flag: false,
            number2: null,
            showConfirm:false,
            showConfirm1:false,
            msg:"",
			FaceId: this.$store.state.FaceId,
			navTitle:'删除',
			timeStart:'',
			schede:this.$store.state.scheduling
        }
    },
    watch: {
        time(v){
            switch(v){
                case 'm':
                    this.list[0] = ['07','08','09','10','11','12']
                break;    
                case 'a':
                    this.list[0] = ['12','13','14','15','16','17','18']
                break;    
                case 'e':
                    this.list[0] = ['18','19','20','21','22']
                break;
            }
        },
    },
    created(){
		console.log(this.schede)
        if(this.$route.query.id){
            this.Id = this.$route.query.id
            const s = this.$store.state.scheduling
           /* if(s.ReserveNums > 0){
                this.$toast(`当前排班已有${s.ReserveNums}人预约，只允许加大接诊人数`)
                this.readonly = true
            }*/
            if(s){
                this.date = s.DutyDate.split(' ')[0]
                this.time1 = s.StartTime
                this.time2 = s.EndTime
                this.number = s.Nums
                this.number2 = s.Nums
                if(s.TimePeriod == 1){
                    this.time = 'm'
                }
                else if(s.TimePeriod == 2){
                    this.time = 'a'
                }
                else if(s.TimePeriod == 3){
                    this.time = 'e'
                }
				if(s.ReserveNums > 0 ){
					if(s.CanClose){
						this.navTitle = '停诊'
					}else{
						this.navTitle = ''
					}
					
				}
            }
        }
		console.log(this.FaceId)
    },
    methods:{
        save(){
            const d = new Date(this.date)
            const s = this.$store.state.scheduling
            
            let WeekDay = d.getDay()
            if(WeekDay === 0){
                WeekDay = 7
            }
            const time1 = this.time1.split(':')
            const time2 = this.time2.split(':')
            if(time2[0]<time1[0]){
                return this.$toast('结束时间应在开始时间之后')
            }
            if(time2[0]==time1[0] && time2[1]<time1[1]){
                return this.$toast('结束时间应在开始时间之后')
            }
            if(time2[0]==time1[0] && time2[1]==time1[1]){
                return this.$toast('结束时间应在开始时间之后')
            }
			const TimePeriod = this.time == 'm'?'上午':this.time == 'a'?'下午':this.time == 'e'?'晚上':''
            const IsOn = this.loop ? 1 : 0
            this.$post('AppointmentAPI/FaceConsultation/SaveDoctorDuty',this.$pick({
                DutyId: this.Id,
                DrId: this.user.User.DoctorId,
				DrName:this.user.User.DoctorName,
				FaceConsultationAddressId:this.FaceId,
				DutyDate: this.date,
				StartTime: this.time1,
				EndTime: this.time2,
				Nums: this.number,
				IsOn,
				TimePeriod,
				
//                 DoctorType: 1,
//                 WeekDay,
 
            })).then(o => {
            	this.$toast('提交成功')
                this.$router.back()
            })
        },
        isDelete(){
        	let i = this.$store.state.scheduling
        	if(i.ReserveNums > 0){
        		this.showConfirm1 = true
                this.msg = `停诊后该条排班信息将被删除（若开启循环排班的话其他排班不受影响，仅删除当前一条排班），已经预约的${i.ReserveNums}名患者将直接进行退款处理。确认停诊吗？`
            }else{
            	this.showConfirm=true
            }
        },
        deleteSch(){
        	//删除排班
        	let i = this.$store.state.scheduling
            this.$post('AppointmentAPI/FaceConsultation/DelDoctorDuty',{
                DrId: this.user.User.DoctorId,
                DutyId: i.DutyId,
            }).then(o => {
            	this.$toast('已删除该排班')
            	this.$store.dispatch('pushscheduling', null)
                this.$router.back()
            })
        },
        change(v){
            this.loop = v
        },
        changeTime(){
			if(this.time === 'm'){
				if(this.arr[0] == '12'){
					 this.list[1] = ['00']	
					 this.list = [this.list[0],['00']]
				}else{
					this.list = [this.list[0],['00','30']]
				}
			}else if(this.time === 'a'){
				if(this.arr[0] == '18'){
					 this.list[1] = ['00']	
					 this.list = [this.list[0],['00']]
				}else{
					this.list = [this.list[0],['00','30']]
				}
			}else{
				if(this.arr[0] == '22'){
					 this.list[1] = ['00']	
					 this.list = [this.list[0],['00']]
				}else{
					this.list = [this.list[0],['00','30']]
				}
			}
			
            if(this.flag === 'start'){
				this.time1 = this.arr.join(':')
            }
            else{
                this.time2 = this.arr.join(':')
            }
        },
        getTime(f){
            this.flag = f
            this.arr = [this.list[0][0],this.list[1][0]]
            console.log(this.arr)
            let i = this.$store.state.scheduling
			this.list = [this.list[0],['00','30']]
            if(this.time === 'm'){
                if(this.flag === 'start'){
                    this.time1 = i && i.TimePeriod == 1 ? i.StartTime : '07:00'
                    this.arr = [this.time1.split(":")[0],this.time1.split(":")[1]]
                }
                else{
                    this.time2 = i && i.TimePeriod == 1 ? i.EndTime : '07:00'
                    this.arr = [this.time2.split(":")[0],this.time2.split(":")[1]]
                }
            }
            else if(this.time === 'a'){
                if(this.flag === 'start'){
                    this.time1 = i && i.TimePeriod == 2 ? i.StartTime : '12:00'
                    this.arr = [this.time1.split(":")[0],this.time1.split(":")[1]]
                }
                else{
                    this.time2 = i && i.TimePeriod == 2 ? i.EndTime : '12:00'
                    this.arr = [this.time2.split(":")[0],this.time2.split(":")[1]]
                }
            }
            else if(this.time === 'e'){
                if(this.flag === 'start'){
                    this.time1 = i && i.TimePeriod == 3 ? i.StartTime : '18:00'
                    this.arr = [this.time1.split(":")[0],this.time1.split(":")[1]]
                }
                else{
                    this.time2 = i && i.TimePeriod == 3 ? i.EndTime : '18:00'
                    this.arr = [this.time2.split(":")[0],this.time2.split(":")[1]]
                }
            }
        },
        reset(){
            this.time1 = null
            this.time2 = null
        },
        cancel(){
            if(this.flag === 'start'){
                this.time1 = null
            }
            else{
                this.time2 = null
                this.flag = false
            }
        }
    }
}



